.contactitem{
    width: 100%;
    // top-down/ left-right
    // margin: 1rem 0;
    &:not(:last-child){
        margin-bottom: 1rem;
    }
    .contact{
        display: flex;
        align-items: center;
        padding: 1rem 0;
        background: #191D2B;
        .right-item{
            margin-left: 2rem;
            h6{
                font-size: 1.4rem;
                font-weight: 500;
            }
        }
        img{
            padding: 1rem;
            border: 1px solid #2e344e;
            // margin-left: 2rem;
        }
    
    }
}

.contactPage{
display: grid;
grid-template-columns: repeat(2, 1fr);
margin: 6rem 0;
.contact-section{
    width: 100%;
}
}

.map-sect{
    height: 100%;
    padding: 2rem;
    background:#191D28;
    iframe{
        width: 100%;
        height: 100%;
    }
}

.contact-section{
 display: flex;
 justify-content: space-between;
 flex-direction: column;
}


